<template>
  <div class="page-login">
    <div class="login-header">
      <div class="logo"></div>
    </div>

    <div class="login-panel">
      <div class="banner">
        <img src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt>
      </div>
      <div class="form">
        <h4 class="tips" v-show="error">
            <i>{{error}}</i>
            <span></span>
        </h4>
        <p class="mobile-box">
          <span class="login">账号登陆</span>
          <span class="mobile-login-box">
            <span class="mobile-login">手机动态码登陆</span>
            <i class="el-icon-mobile"></i>
          </span>
        </p>
        <el-input :class="[{'error':error && !userName}]" placeholder="请输入账号" v-model="userName" show-password prefix-icon="profile"></el-input>
        <el-input :class="[{'error':error && !userName}]" type="password" placeholder="请输入密码" v-model="passWord" show-password prefix-icon="password"></el-input>
        <div class="foot">
          <a>忘记密码?</a>
        </div>
        <el-button class="btn-login" type="primary" @click="submit">登陆</el-button>
        <p class="reg">
          <span>还没有账号？</span>
          <a href>
            <router-link to="/">免费注册</router-link>
          </a>
        </p>

        <div class="oauth-wrapper">
          <p class="title-wrapper">
            <span class="title">用合作网站登陆</span>
          </p>
          <div class="oauth">
            <div class="oauth__link--weibo"></div>
            <div class="oauth__link"></div>
          </div>
        </div>
      </div>
    </div>

    <footer>
      <ul>
        <li class="first">
          <a rel="nofollow" href="https://about.meituan.com/about.html">关于美团</a>
        </li>
        <li>
          <a rel="nofollow" href="https://zhaopin.meituan.com/">加入我们</a>
        </li>
        <li>
          <a rel="nofollow" href="http://emis.meishi.meituan.com/merchantsSettled">商家入驻</a>
        </li>
        <li>
          <a rel="nofollow" href="https://www.meituan.com/help/faq">帮助中心</a>
        </li>
        <li class="last">
          <a href="http://meituan.com/mobile">美团手机版</a>
        </li>
      </ul>
      <p>
        ©
        <span>2019</span>
        <a href="https://www.meituan.com">美团网团购</a>
        meituan.com
        <a href="http://www.miibeian.gov.cn/" target="_blank">京ICP证070791号</a>
        京公网安备11010502025545号
      </p>
    </footer>
  </div>
</template>
<script>

import Api from "@/api/index.js";
const api = new Api();

import {mapActions} from 'vuex'
export default {
  data() {
    return {
      userName:'',
      passWord:'',
      error:''
    };
  },
  methods:{
    ...mapActions(['haveLogin']),
    submit(){
        if(this.userName == ''){
            this.error = '请输入账号'
            return false
        }
        if(this.passWord == ''){
            this.error = '请输入密码'
            return false
        }
        if(this.userName == ''&& this.passWord == ''){
            this.error = '请输入账号密码'
            return false
        }

        api.login({
            params:{
                userName: this.userName,
                password: this.passWord,
                  }
            }).then((res)=>{

          if(res.status == 200){
              this.error = res.data.msg;
              this.$router.push('/')
              this.haveLogin('已登陆')
          }else{
              this.error = res.data.msg
          }
        })
    }
  }
};
</script>
<style lang='scss'>
@import "@/assets/css/login/index.scss";
</style>



